// src/components/TicketPDFTemplate.jsx
import React from "react";
import { QrCode } from "lucide-react";

const TicketPDFTemplate = ({ ticket }) => {
  const formatPaymentMethod = (method) => {
    const methods = {
      cash: "Espces",
      card: "Carte bancaire",
      "mobile-money": "Mobile Money",
    };
    return methods[method] || method;
  };

  return (
    <div
      id="ticket-pdf-template"
      style={{
        width: "70mm",
        padding: "10px",
        fontFamily: "Arial, sans-serif",
        backgroundColor: "white",
      }}
    >
      {/* En-tte */}
      <div style={{ textAlign: "center", marginBottom: "10px" }}>
        <h2
          style={{
            margin: 0,
            fontSize: "16px",
            fontWeight: "bold",
            color: "#e53e3e",
          }}
        >
          KIDS EVENTS
        </h2>
        <h3 style={{ margin: 0, fontSize: "14px", color: "#2d3748" }}>
          VILLAGE DE NOL
        </h3>
      </div>

      {/* Sparateur */}
      <hr style={{ border: "1px solid #000", margin: "10px 0" }} />

      {/* Informations de base */}
      <div style={{ marginBottom: "15px" }}>
        <p style={{ margin: "2px 0", fontSize: "10px" }}>
          <strong>ID:</strong> {ticket.id}
        </p>
        <p style={{ margin: "2px 0", fontSize: "10px" }}>
          <strong>Date:</strong>{" "}
          {new Date(ticket.dateCreation).toLocaleDateString("fr-FR")}
        </p>
        <p style={{ margin: "2px 0", fontSize: "10px" }}>
          <strong>Heure:</strong>{" "}
          {new Date(ticket.dateCreation).toLocaleTimeString("fr-FR")}
        </p>
      </div>

      {/* Dtails du ticket */}
      <div style={{ textAlign: "center", marginBottom: "15px" }}>
        <h4
          style={{ margin: "0 0 5px 0", fontSize: "12px", fontWeight: "bold" }}
        >
          DTAILS DU TICKET
        </h4>
        <p style={{ margin: "2px 0", fontSize: "10px" }}>
          <strong>Billets:</strong> {ticket.nombreBillets} personne(s)
        </p>
        <p style={{ margin: "2px 0", fontSize: "10px" }}>
          <strong>Prix total:</strong> {ticket.prixTotal.toLocaleString()} FCFA
        </p>
        <p style={{ margin: "2px 0", fontSize: "10px" }}>
          <strong>Paiement:</strong> {formatPaymentMethod(ticket.typePaiement)}
        </p>
        {ticket.codePromo && (
          <p style={{ margin: "2px 0", fontSize: "10px" }}>
            <strong>Code promo:</strong> {ticket.codePromo}
          </p>
        )}
      </div>

      {/* Zone QR Code */}
      <div style={{ textAlign: "center", margin: "20px 0" }}>
        <div
          style={{
            border: "2px solid #000",
            padding: "10px",
            display: "inline-block",
            borderRadius: "5px",
          }}
        >
          <p
            style={{
              margin: "0 0 5px 0",
              fontSize: "10px",
              fontWeight: "bold",
            }}
          >
            SCANNEZ  L'ENTRE
          </p>
          <div
            style={{
              width: "80px",
              height: "80px",
              border: "1px dashed #666",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              margin: "0 auto",
              backgroundColor: "#f7fafc",
            }}
          >
            <QrCode size={40} color="#4a5568" />
          </div>
          <p
            style={{
              margin: "5px 0 0 0",
              fontSize: "8px",
              wordBreak: "break-all",
              fontFamily: "monospace",
            }}
          >
            {ticket.codeUnique}
          </p>
        </div>
      </div>

      {/* Pied de page */}
      <div style={{ textAlign: "center", marginTop: "15px" }}>
        <p
          style={{
            margin: 0,
            fontSize: "8px",
            fontStyle: "italic",
            color: "#718096",
          }}
        >
          Prsentez ce ticket  l'entre de l'vnement
        </p>
        <p style={{ margin: "5px 0 0 0", fontSize: "7px", color: "#a0aec0" }}>
          Ticket gnr le {new Date().toLocaleString("fr-FR")}
        </p>
      </div>
    </div>
  );
};

export default TicketPDFTemplate;
