// src/services/pdfService.js
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

export const pdfService = {
  // Gnrer un PDF  partir d'un lment HTML
  async generateTicketPDF(ticketData, element = null) {
    const doc = new jsPDF({
      orientation: "portrait",
      unit: "mm",
      format: [80, 150], // Format ticket standard
    });

    try {
      if (element) {
        // Utiliser html2canvas pour capturer l'lment
        const canvas = await html2canvas(element, {
          scale: 2,
          useCORS: true,
          logging: false,
        });

        const imgData = canvas.toDataURL("image/png");
        doc.addImage(imgData, "PNG", 5, 5, 70, 140);
      } else {
        // Gnrer le PDF manuellement si aucun lment n'est fourni
        this.generateManualPDF(doc, ticketData);
      }

      // Sauvegarder le PDF
      const fileName = `ticket-${ticketData.id}.pdf`;
      doc.save(fileName);

      return fileName;
    } catch (error) {
      console.error("Erreur gnration PDF:", error);
      throw new Error("Erreur lors de la gnration du PDF");
    }
  },

  // Gnration manuelle du PDF
  generateManualPDF(doc, ticket) {
    // En-tte
    doc.setFontSize(16);
    doc.setFont("helvetica", "bold");
    doc.text("KIDS EVENTS", 40, 15, { align: "center" });

    doc.setFontSize(12);
    doc.setFont("helvetica", "normal");
    doc.text("VILLAGE DE NOL", 40, 22, { align: "center" });

    // Ligne sparatrice
    doc.line(10, 25, 70, 25);

    // Informations du ticket
    doc.setFontSize(10);
    doc.text(`ID: ${ticket.id}`, 10, 35);
    doc.text(
      `Date: ${new Date(ticket.dateCreation).toLocaleDateString("fr-FR")}`,
      10,
      42
    );
    doc.text(
      `Heure: ${new Date(ticket.dateCreation).toLocaleTimeString("fr-FR")}`,
      10,
      49
    );

    // Dtails
    doc.setFont("helvetica", "bold");
    doc.text("DTAILS DU TICKET", 40, 60, { align: "center" });
    doc.setFont("helvetica", "normal");

    doc.text(`Billets: ${ticket.nombreBillets}`, 10, 70);
    doc.text(`Prix: ${ticket.prixTotal.toLocaleString()} FCFA`, 10, 77);
    doc.text(
      `Paiement: ${this.formatPaymentMethod(ticket.typePaiement)}`,
      10,
      84
    );

    if (ticket.codePromo) {
      doc.text(`Code promo: ${ticket.codePromo}`, 10, 91);
    }

    // QR Code area
    doc.setFont("helvetica", "bold");
    doc.text("SCANNEZ  L'ENTRE", 40, 105, { align: "center" });

    // Placeholder pour QR code - vous pouvez ajouter le vrai QR code ici
    doc.rect(25, 110, 30, 30);
    doc.setFontSize(8);
    doc.text("QR CODE", 40, 125, { align: "center" });
    doc.text(ticket.codeUnique, 40, 132, { align: "center" });

    // Pied de page
    doc.setFontSize(8);
    doc.text("Prsentez ce ticket  l'entre", 40, 145, { align: "center" });
  },

  formatPaymentMethod(method) {
    const methods = {
      cash: "Espces",
      card: "Carte bancaire",
      "mobile-money": "Mobile Money",
    };
    return methods[method] || method;
  },

  // Gnrer un template HTML pour le ticket
  generateTicketTemplate(ticket) {
    return `
      <div style="width: 70mm; padding: 10px; font-family: Arial, sans-serif;">
        <div style="text-align: center; margin-bottom: 10px;">
          <h2 style="margin: 0; font-size: 16px; font-weight: bold;">KIDS EVENTS</h2>
          <h3 style="margin: 0; font-size: 14px;">VILLAGE DE NOL</h3>
        </div>
        
        <hr style="border: 1px solid #000; margin: 10px 0;">
        
        <div style="margin-bottom: 15px;">
          <p style="margin: 2px 0; font-size: 10px;"><strong>ID:</strong> ${
            ticket.id
          }</p>
          <p style="margin: 2px 0; font-size: 10px;"><strong>Date:</strong> ${new Date(
            ticket.dateCreation
          ).toLocaleDateString("fr-FR")}</p>
          <p style="margin: 2px 0; font-size: 10px;"><strong>Heure:</strong> ${new Date(
            ticket.dateCreation
          ).toLocaleTimeString("fr-FR")}</p>
        </div>
        
        <div style="text-align: center; margin-bottom: 15px;">
          <h4 style="margin: 0 0 5px 0; font-size: 12px; font-weight: bold;">DTAILS DU TICKET</h4>
          <p style="margin: 2px 0; font-size: 10px;"><strong>Billets:</strong> ${
            ticket.nombreBillets
          }</p>
          <p style="margin: 2px 0; font-size: 10px;"><strong>Prix total:</strong> ${ticket.prixTotal.toLocaleString()} FCFA</p>
          <p style="margin: 2px 0; font-size: 10px;"><strong>Paiement:</strong> ${this.formatPaymentMethod(
            ticket.typePaiement
          )}</p>
          ${
            ticket.codePromo
              ? `<p style="margin: 2px 0; font-size: 10px;"><strong>Code promo:</strong> ${ticket.codePromo}</p>`
              : ""
          }
        </div>
        
        <div style="text-align: center; margin: 20px 0;">
          <div style="border: 2px solid #000; padding: 10px; display: inline-block;">
            <p style="margin: 0 0 5px 0; font-size: 10px; font-weight: bold;">SCANNEZ  L'ENTRE</p>
            <div style="width: 80px; height: 80px; border: 1px dashed #666; display: flex; align-items: center; justify-content: center; margin: 0 auto;">
              <span style="font-size: 8px;">QR CODE</span>
            </div>
            <p style="margin: 5px 0 0 0; font-size: 8px; word-break: break-all;">${
              ticket.codeUnique
            }</p>
          </div>
        </div>
        
        <div style="text-align: center; margin-top: 15px;">
          <p style="margin: 0; font-size: 8px; font-style: italic;">Prsentez ce ticket  l'entre de l'vnement</p>
        </div>
      </div>
    `;
  },
};

export default pdfService;
